<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- card  -->
    <el-card>
      <!--  -->
      <el-input placeholder="" v-model="orderQuery.query" style="width: 30%"
                clearable @clear="getOrderList">
        <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
      </el-input>
      <!--   -->
      <el-table :data="orderList.goods" boder>
        <!--   -->
        <el-table-column type="index" label="#">
          <template slot-scope="scope">
            <span>{{(orderQuery.pagenum - 1) * orderQuery.pagesize + scope.$index + 1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="90px"></el-table-column>
        <el-table-column label="" width="90px">
          <template slot-scope="scope">
            <el-tag type="danger" size="small" v-if="scope.row.pay_status === '0'"></el-tag>
            <el-tag type="success" size="small" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="90px">
          <template slot-scope="scope">
            <el-tag size="small" effect="dark" v-if="scope.row.is_send === ''"></el-tag>
            <el-tag size="small" type="danger" effect="dark" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="create_time" width="180px">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template>
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button size="mini" icon="el-icon-edit" type="primary" @click="editAddressDialog"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top">
              <el-button size="mini" icon="el-icon-location-information" type="success" @click="showProgressDialog"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="orderQuery.pagenum" :page-sizes="[10, 15, 20, 30]"
                     :page-size="orderQuery.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addressDialogVisible" width="30%"
      @close="addressDialogClosed">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef"
        label-width="100px">
        <!--   -->
        <el-form-item label="/" prop="address1">
          <el-cascader v-model="addressForm.address1" :options="cityData" :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <!--    -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressDialogVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo"
           :key="index" :timestamp="activity.time" :color="activity.color">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data () {
    return {
      // 
      orderList: [],
      // 
      orderQuery: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: 0,
      // 
      addressDialogVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      // 
      progressDialogVisible: false,
      // 
      progressInfo: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', { params: this.orderQuery })
      if (res.meta.status !== 200) { return this.$message.error('') }
      console.log(res.data)
      this.orderList = res.data
      this.total = res.data.total
    },
    // 
    handleSizeChange (newSize) {
      this.orderQuery.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange (newPage) {
      this.orderQuery.pagenum = newPage
      this.getOrderList()
    },
    // 
    editAddressDialog () {
      this.addressDialogVisible = true
    },
    // 
    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async showProgressDialog () {
      // 1106975712662
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      res.data.forEach(item => {
        // 
        this.$set(item, 'color', '#0bbd87')
        // console.log(item)
      })
      // console.log(res.data)
      this.progressInfo = res.data
      this.progressDialogVisible = true
    }
  }
}
</script>

<style scoped>
.el-table {
  margin-bottom: 15px;
}
  .el-cascader {
    width: 100%;
  }
</style>
